<script>
    import Axios from "../util/Request"
    import qs from "qs";
    import Api from "../api/Api";
    import Action from  "../stores/actions";
    export default {
        name: "Login.vue",
        data(){
            return {
                phone_reg:false,
                error_common:"",
                github_clientId:"0ee189d28e8bacd409cb",
                github_SecretId:"127091a6cd7f70a575d4a1b7d8c1915ec020db21",
                github_code:"",
                login_form: {
                    username:"",
                    password_err:"",
                    username_err:"",
                    password:"",

                },
                threeForm:{
                    username: "",
                    isthree:"1",
                },
                pwd_reg:{
                    username:"",
                    password:"",
                    password2:"",
                },
                phone_reg:{
                    phone:"",
                    code:"",
                }
            }
        },
        methods:{

            search_params(value) {
                let path = location.href;
                if(path.includes("code")){
                    let path_params = path.split("?")[1];
                    let path_params_list = path_params.split("&");
                    for (let i = 0; i < path_params_list.length; i++) {
                        let ppp = path_params_list[i].split("=");
                        if (ppp[0] == value) {
                            return ppp[1];
                        } else {
                            return false;
                        }
                    }
                }
            },
            login(){
             this.$router.push({
                 path:"/login",
             })
            },
            register(){
                if(this.pwd_reg.password == this.pwd_reg.password2){
                    this.pwd_reg["isthree"] = "0";
                    Api.API_REGISTER(this.pwd_reg).then(res=>{
                        if(res.status==200){
                            this.$store.dispatch(Action.ACTION_TOKEN_SET, res.data.data.Token);
                            this.$store.dispatch(Action.ACTION_USER_SET, JSON.stringify(res.data.data.user));
                            this.$router.push({
                                path: "/",
                            })
                        }else{
                            this.error_common="注册失败？？"+res.data.message;
                        }
                    }).catch(error=>{
                        this.error_common="注册失败？？"+ error;
                    })
                }else{
                    this.error_common="两次密码不一致"
                }

            }
        },
        created() {
            // this.loginGithub();
        }
    }
</script>

